.main-sidebar {
  position: absolute;
  top: 0;
  left: 0;
  width: 230px;
  min-height: 100%;
  padding-top: 50px;
  background-color: #fff;
  transition: transform 0.3s ease-in-out, width 0.3s ease-in-out;
  z-index: 810;

  .sidebar {



    .sidebar__profile {
      position: relative;
      padding-bottom:10px;
      padding-left: 5px;
      border-bottom:1px solid #fff;

        &:after {
          content: "";
          position: absolute;
          width: 100%;
          bottom:0;
          left:0;
          height: 1px;
          background: #e0e0e0;

        }

      .sidebar__profile__greetings {
        color: $color-sidebar-greeting;
        font-size: $font-sidebar-greeting-size;
        text-align: center;
        margin: 24px 0 15px 0;
      }

      .sidebar__profile__avatar {
        .sidebar__profile__avatar_image--wrapper {
          display: inline-block;
          text-align: center;

          .sidebar__profile__avatar_image {
            border-radius: 50%;
            width: $avatar-dimension;
            height: $avatar-dimension;
          }

          .sidebar__profile__avatar_change-avatar-text {
            width: 70px;
            line-height: 1;
            padding-top: 5px;
            text-decoration: none;

            a {
              color: $color-sidebar-change-avatar-text;

              &:hover {
                color: darken($color-sidebar-change-avatar-text, 8%);
              }
            }



          }

        }

        .sidebar__profile__avatar_name {
          width: 140px;
          display: inline-block;
          font-size: $font-sidebar-avatar-name-text;
          line-height:1.425;
          vertical-align: middle;
          padding-left: 5px;

          a {
            position: relative;
            top: 2px;
            left: 3px;
          }

        }
      }
    }

    .abonement__profile {
      .sidebar__profile__greetings {
      }

      .sidebar__profile_title {
        font-size: 24px;
        color: #333;
        margin-bottom: 3px;
      }

    }

    .sidebar__scores {
      padding:10px 0 5px 0;
      text-align: center;
      border-bottom: 1px solid #1b7f53;

      .sidebar__scores__label {
        font-size: $font-sidebar-score-label;
      }

      .sidebar__scores__count {
        color:$color-sidebar-score-count;
        font-size: $font-sidebar-score-count;
        font-weight: bold;
      }
    }

    .sidebar__nav {
      .sidebar__nav__links {
        .sidebar__nav__links_link {
          a {
            color: #fff;
            background: #27ac71;
            text-transform: uppercase;
            font-weight: bold;
            padding: 11px 44px 11px 17px;
            cursor: pointer;
            position: relative;
            border-width: 1px 0;
            border-style: solid;
            border-color: #68c59c;
            border-bottom-color: #1b7f53;
            display: block;
            text-decoration: none;
            transition: all .1s linear;

            &:hover {
              background-color: #1f905e;
              border-color: #63b28f;
              border-bottom-color: #1a6f49;
            }
          }
        }
      }
    }

    .sidebar__banners {
      padding: 0 0 20px 0;
      .sidebar__banner_list {
        .sidebar__banner_list_item {
          padding-top: 20px;
          text-align: center;
          img {
            max-width: 100%;
          }
        }
      }
    }
  }
}


