.rentFieldSchedule__modal_team {

  .modal-content {

    border: none;

    border-radius: 10px;
    background: $color-base-dark-blue;

    .rentfieldschedule__modal__create_team {

      .modal__create_team__header {
        display: flex;
        min-height: 100px;
        padding: 10px 25px 5px 25px;
        align-items: center;
        background: url('../images/create_team.jpg') no-repeat;
        border-radius: 8px 8px 0 0;

        h3 {
          color: #fff;
          font-weight: bold;
          font-size: 30px;
          margin-top: 5px;
        }

      }

      .modal__create_team__content {
        .content__add {
          color: #fff;
          margin: 15px 0 0 0;

          .content__add_info_text {
            position: relative;
            display: inline-block;
            margin-right: 25px;
            font-size: 18px;

            &:before {
              content: "";
              position: absolute;
              width: 2px;
              height: 100%;
              left: -8px;
              top:0;
              background: $color-base-link;
            }
          }

          .content__add_info_text--phone{
            position: relative;
            display: block;
            margin-right: 25px;
            font-size:18px;

            &:before {
              content: "";
              position: absolute;
              width: 2px;
              height: 100%;
              left: -8px;
              top:0;
              background: $color-base-link;
            }
          }

          .content__add__header_text {
            position: relative;
            font-size: 18px;
            display: block;
            text-align: center;
            z-index:120;

            span{
              background:$color-base-dark-blue;
              padding:0 10px;
            }

            &:before {
              content: "";
              position: absolute;
              width:100%;
              height: 2px;
              background: $color-base-link;
              top:50%;
              left:0;
              z-index: -1;

            }
          }

          .content__add__content {
            margin: 20px 0 15px 15px;

            .left {
              float: left;
              width: 49%;
            }

            .right {
              float: right;
              width: 49%;

              p {
                text-align: center;
                font-size: 16px;
                margin-right: 20px;
              }

            }

            .content__add__content__info_team {
              font-size: 14px;
              color: #b2b2b2;
              margin-top: 7px;
            }

            .icon--phone {
              position: relative;
              width: 35px;
              height: 40px;
              margin: 0 auto;
              background: url('../images/i/message.png') no-repeat;
            }

          }

          input {
            margin-right:15px;
            color: #000;
            padding:7px 0 7px 5px;
            border-radius:5px;
            border: none;

            &[name=teamname] {
              width: 200px;

              &.ng-dirty.ng-invalid {
                border: 1px solid red;
              }

            }


          }

        }

        .content__table {

          .header {
            position: relative;
            padding-left: 60px;
            &:before, &:after {
              content: "";
              position: absolute;
            }

            &:before {
              width: 16px;
              height: 22px;
              background: url("../images/i/my-team-icon.png") no-repeat;
              top: 7px;
              left: 15px;
            }
          }

          .content__table_link--remove, .content__table_link--notify {
            color: #fff;

            &:hover {
              color: darken(#fff, 25);
            }
          }

        }

      }

      .modal__create_team__footer {
        padding: 15px;
        text-align: right;

        .btn-goback {
          float: left;
        }

      }

    }
  }
}