.grid-view {

  table {
    border: none;
    thead {
      background: $color-base-link;

      tr {
        th {
          border: none;
          color: #fff;
        }
      }
    }

    tbody {
      tr {
        &:nth-of-type(odd) {
          background: darken(#fff, 3);
        }
        &:nth-of-type(even) {
          background: #fff;
        }
      }
    }
  }

}