.dark_blue {
  margin-top: 25px;
  .header {
    height: 35px;
    background: $color-base-link;
    text-transform: uppercase;
    position: relative;
    font-size: 17px;
    color: #fff;
    text-align: left;
    line-height: 37px;
  }

  table {

    width: 100%;
    padding: 13px 0 15px;
    margin-bottom: 20px;
    margin-top: 8px;
    border-width: 0;
    background: #091e2d;
    font-size: inherit;

    thead {
      tr {
        color: $color-base-link;

        th {
          padding: 2px 10px 4px;
          text-transform: uppercase;
          text-align: left;
          font-weight: normal;
          background: none;
        }
      }
    }

    tbody {
      color: #fff;
      tr {

        td {

          padding: 2px 10px 4px;

          &:first-child {
            color: rgba(255, 255, 255, 0.3);
            padding-left: 20px;
          }

          &:last-child {
            padding-right: 10px;
          }

        }

        &:nth-child(odd) td {
          background: none;
        }

        &.current {
          background: #000;
          color: $color-base-link;
        }
      }
    }
  }

  &.center_th {
    th {
      text-align: center;
    }
  }

  &.myMatches {
    .header {
      position: relative;
      padding-left: 50px;
      &:before {
        content: "";
        position: absolute;
        width: 16px;
        height: 22px;
        background: url("../images/i/my-team-icon.png") no-repeat;
        top: 7px;
        left: 15px;
      }
    }

    .matches__empty {
      padding-top:10px;
    }


  }
}

