// Название класса site__index нужно будет потом поменять, на соотвествующий другой класс
// так как сейчас все считывание происходит из site/index. Потом будет скорее всего rent/index

// Вообще это весьма спорный прием для работы, но пока оставляем так. Возможно что то в будущем
// придумается получше
.content-wrapper.rent__index {

  background: url('/images/bg_rent.jpg') no-repeat;
  background-size: cover;

  .content {
    background: none;
  }

  .content-header {
    //background: #091e2d;
  }

  .rentfield__container {
    padding: 30px 0 15px 0;
    background: #091e2d;

    .rent_options {
      padding-top: 30px;
      a {
        position: relative;
        color: #fff;

        &:before {
          content: "";
          position: absolute;
          width: 100%;
          height: 175px;
          border: 2px solid #fff;
          background: rgba(9, 30, 45, .5);
          z-index: 110;
        }

        .title {
          position: absolute;
          top: -10px;
          margin: 0 auto;
          left: 0;
          right: 0;
          font-size: 20px;
          text-transform: uppercase;
          font-weight: bold;
          z-index: 150;
        }

        &:hover:before {
          border: 2px solid #27ac71;
        }

      }

      p {
        color: #fff;
      }

      .caption {
        padding: 0 20px;
      }

    }
  }

}