.modal--notification {

  .modal-content {

    border: none;

    border-radius: 10px;
    background: $color-base-dark-blue;

    .modal-header {
      display: flex;
      min-height: 100px;
      padding: 10px 25px 5px 25px;
      align-items: center;
      background: url('../images/create_team.jpg') no-repeat;
      background-size: cover;
      border-radius: 8px 8px 0 0;

      .close {
        position: absolute;
        top: 10px;
        right: 30px;
        color: #fff;
        opacity: 1;
        font-size: 32px;
      }

      h4 {
        color: #fff;
        font-weight: bold;
        font-size: 30px;
        margin-top: 5px;
      }

    }

    .modal-body {
      color: #fff;

    }

    .modal-footer {
      text-align: left;

      button {
        text-transform: uppercase;
      }

      #team_accept {
        float: right;
      }
    }

    .modal__create_team__footer {
      padding: 15px;
      text-align: right;

      .btn-goback {
        float: left;
      }

    }

  }
}