.rentFieldSchedule__modal {

  .modal-content {

    border-radius: 10px;

    .rentfieldschedule__modal_add_order {

      .modal__orderfield__header {
        background: $color-modal-header;
        display: flex;
        min-height: 80px;
        padding: 10px 25px 5px 25px;
        align-items: center;
        border-radius: 8px 8px 0 0;
        & > div {
          flex-grow: 1;
        }

        .modal__orderfield__info {
          .modal__orderfield_title {
            font-size: $font-modal-header;
            margin: 0;
          }
          .modal__orderfield_date {
            line-height: $lh-header;

          }
          .modal__orderfield_date_time {
            font-weight: bold;
          }
        }

        .modal__orderfield__team {
          position: relative;

          .modal__orderfield__team_title {
            color: $color-header-info;
            font-size: $font-modal-header-info;
            line-height: $font-modal-header;
          }

          .modal__orderfield__team_icon {
            background: url('/images/add_team_icon.png') no-repeat;
            background-size: contain;
            bottom: 0;
            height: 20px;
            left: -25px;
            position: absolute;
            width: 20px;
          }

          .modal__orderfield__team_name {
            color: $color-base-link;
            line-height: $lh-header;
            position: relative;
            text-decoration: underline;

            &:hover {
              text-decoration: none;
            }
          }
        }

        .modal__orderfield__total {
          .modal__orderfield__total_title {
            line-height: $font-modal-header;
          }

          .modal__orderfield__total_count {
            font-size: 40px;
          }

        }

      }

      .modal__orderfield__add {

        padding: 0 25px 0 25px;

        & > div {
          display: inline-block;
          vertical-align: middle;
        }

        .modal__orderfield__add_info {

          .modal__orderfield__add_info_size, .modal__orderfield__add_info__available_size {
            font-size: 24px;
          }

        }

        .modal__orderfield__add_fields {
          padding-left: 50px;
          padding-top: 15px;
          position: relative;
          .modal__orderfield__add_fields_bg {
            background: url('/images/field.png') no-repeat;
            background-size: cover;
            height: 95px;
            position: relative;
            width: 70px;

            & div {
              background: rgba(0, 0, 0, .5);
              cursor: pointer;
              height: 45px;
              position: absolute;
              transition: background .1s ease-in-out;
              width: 32px;

              &:hover {
                background: rgba(0, 0, 0, .7);

                i {
                  color: rgba(255, 255, 255, 1);
                }

              }

            }

            .modal__orderfield__add_fields__element {
              i {
                color: rgba(255, 255, 255, .7);
                padding: 15px 10px;
                position: relative;
                z-index: 1000;
              }

              &.active {
                display: none;
              }

            }

            .element_1 {
              left: 2px;
              top: 2px;
            }

            .element_2 {
              left: 36px;
              top: 2px;
            }

            .element_3 {
              left: 2px;
              top: 48px;
            }

            .element_4 {
              left: 36px;
              top: 48px;
            }

          }
        }

        .modal__orderfield__add_text {
          float: right;
          padding-top: 40px;
          position: relative;
          text-align: center;
          width: 270px;

          &:before {
            background: #f5f5f5;
            content: "";
            height: 100px;
            left: -20px;
            position: absolute;
            top: 15px;
            width: 1px;
          }

        }

      }

      .modal__orderfield__footer {
        padding: 15px;
        text-align: right;
      }

    }
  }
}