.rentfieldschedule {

  padding: 5px 15px 0 15px;

  .date_wrapper {
    overflow: hidden;
    position: relative;
  }

  .date_wrapper__gradient__left {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    background-image: linear-gradient(to left, transparent, white);
    opacity: 1;
    z-index: 110;
    width: 105px;

    &:hover {
      background-image: linear-gradient(to left, transparent, darken(white, 5));
    }
  }

  .date_wrapper__gradient__right {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    background-image: linear-gradient(to right, transparent, white);
    opacity: 1;
    z-index: 110;
    width: 105px;

    &:hover {
      background-image: linear-gradient(to right, transparent, darken(white, 5));
    }
  }

  .date {
    width: 1250px;
    overflow: hidden;
    position: relative;
    list-style: none;
    margin: 0;
    padding: 0;

    .date_item {
      float: left;
      margin: 0 -1px 0 0;
      position: relative;
      z-index: 100;

      &:hover {
        background: #ededed;
      }

      .date_item__link {
        position: relative;
        display: block;
        width: 156px;
        height: 63px;
        padding: 0 0 0 34px;
        background: #f7f7f7;
        border: 1px solid #d9d9d9;
        border-bottom: none;

        &:hover {
          text-decoration: none;
          background: #ededed;
        }

        &:focus {
          text-decoration: none;
        }

        .date_item__time {
          display: block;
          font-size: 14px;
          line-height: 1.2;
          color: #666;
          padding: 16px 0 3px;
        }

        .date_item__day {
          font-size: 11px;
          line-height: 1.2;
          color: #999;
        }

      }

      &.active {
        .date_item__link {
          background: #fff;
          border-top-color: #cb6847;
          cursor: default;
          z-index: 2;

          &:before {
            content: "";
            display: block;
            position: absolute;
            top: -1px;
            left: -1px;
            right: -1px;
            background: #ff781a;
            border: 1px solid #d96616;
            border-width: 1px 1px 0;
            height: 4px;
          }

          .date_item__time {
            font-weight: 700;
            color: #4d4d4d;
          }

        }
      }

    }

  }

  .schedule_items {
    border-top: 1px solid #d9d9d9;
    transition: height .3s ease;

    .schedule_item {
      background: #f7f7f7;
      border: 1px solid #d9d9d9;
      margin: -1px 0 0;
      position: relative;

      &:hover {
        background: #fff;
        position: relative;
      }

      &:first-child {
        .schedule_item_inner {
          border-top: none;
          color: #000;
        }
      }

      .schedule_item_inner {
        padding: 21px 35px 14px 30px;
        overflow: hidden;

        .schedule_name {
          float: left;
          width: 272px;
          margin: 0 0 18px;
        }

        .schedule_title {
          font-size: 17px;
          font-weight: 700;
          color: #50545f;
          line-height: 18px;
          margin: 8px 0 6px;

          a {
            color: #50545F;
            text-decoration: none;
          }
        }

        .schedule_info {
          font-size: 13px;
          line-height: 1.2;
          color: #939393;
        }

        .schedule_list {
          margin-left: 304px;

          .schedule_list__title {

            &:first-child {
              padding-top: 11px;
            }

            font-size: 14px;
            color: #888;
            padding: 7px 13px 9px;
            max-height: 10000em;
          }

          .schedule_list__item {
            list-style: none;
            margin: 0;
            padding: 0;

            .schedule_btn {
              display: inline-block;
              font-size: 14px;
              color: #666;
              margin: 0 2px 7px 0;
              line-height: 33px;
              width: 60px;
              height: 35px;
              text-align: center;
              padding: 0;
              box-sizing: border-box;
              vertical-align: top;
              border-radius: 3px;
              cursor: pointer;
              box-shadow: 0 1px 0 #cacaca;
              border: 1px solid #e6e6e6;
              background: #fff;
              background: -webkit-linear-gradient(top, #fefefe 0, #f1f1f1 100%);
              background: linear-gradient(180deg, #fefefe 0, #f1f1f1 100%);

              &:hover {
                box-shadow: 0 3px 0 #d9d9d9;
                border-color: #df6e49;
                background: #ff781a;
                background: -webkit-linear-gradient(top, #ff8223 0, #ff6d16 100%);
                background: linear-gradient(180deg, #ff8223 0, #ff6d16 100%);
                color: #fff;
              }
            }

          }

        }

      }

    }

  }

}