.abonement-wrapper {
  padding: 20px 0;
}

.abonement {
  border: 2px solid #f1f1f1;
  border-radius: 10px;
  padding-bottom: 20px;


  .abonement_top {
    padding: 20px;
    border-top-left-radius: 7px;
    border-top-right-radius: 7px;
    background-color: #f9f9fa;
    text-align: center;
  }

  .abonement__title {
    font-size: 30px;
  }

  .abonement__price {
    position: relative;

    .abonement__price__num {
      font-size: 40px;
    }

    .abonement__price__symbol {
      position: absolute;
      font-size: 20px;
      top: 20px;
    }
  }

  .abonement__info {
    font-size: 20px;
    padding-top: 15px;

    .abonement__info_days {
      display: block;
      padding-bottom: 10px;
    }

    .abonement__info_trainings {
      display: block;
      padding-bottom: 10px;
    }
  }


  footer {
    .btn-buy {
      padding: 12px 25px;
      border: 1px solid #2f8cff;
      border-radius: 5px;
      background-color: transparent;
      font-family: adelle-sans, sans-serif;
      color: #2f8cff;
      font-size: 14px;

      &:hover {
        background-color: rgba(47, 140, 255, .05);
      }
    }
  }

}