.team__index {
  .teamtable {
    background: $color-base-dark-blue;
    color: #fff;

    .content__add {
      margin: 15px 0 0 0;

      .content__add__content {
        margin: 20px 0 15px 15px;

        .left {
          float: left;
          width: 49%;
          .content__add__header_text {
            position: relative;
            font-size: 18px;
            display: block;
            text-align: center;
            z-index:120;

            span{
              background:$color-base-dark-blue;
              padding:0 10px;
            }

            &:before {
              content: "";
              position: absolute;
              width:100%;
              height: 2px;
              background: $color-base-link;
              top:50%;
              left:0;
              z-index: -1;

            }
          }
        }

        .right {
          float: right;
          width: 49%;

          p {
            text-align: center;
            font-size: 16px;
            margin-right: 20px;
          }

        }

        .content__add__content__info_team {
          font-size: 14px;
          color: #b2b2b2;
          margin-top: 7px;
        }

        .icon--phone {
          position: relative;
          width: 35px;
          height: 40px;
          margin: 0 auto;
          background: url('../images/i/message.png') no-repeat;
        }

      }

      input {
        margin-right:15px;
        color: #000;
        padding:7px 0 7px 5px;
        border-radius:5px;
        border: none;

        &[name=teamname] {
          width: 200px;
        }


      }

    }

    .content__table {

      .header {
        position: relative;
        padding-left: 60px;
        &:before, &:after {
          content: "";
          position: absolute;
        }

        &:before {
          width: 16px;
          height: 22px;
          background: url("../images/i/my-team-icon.png") no-repeat;
          top: 7px;
          left: 15px;
        }
      }

      .content__table_link--remove, .content__table_link--notify {
        color: #fff;

        &:hover {
          color: darken(#fff, 25);
        }
      }

    }

    .team__hint {
      padding:15px;
      font-size: 20px;
    }

    .btn-success, .btn-danger {
      margin: 10px;
    }

  }
}