  position: absolute;
  top: 0;
  left: 0;
  width: 230px;
  min-height: 100%;
  padding-top: 50px;
  background-color: #fff;
  transition: transform 0.3s ease-in-out, width 0.3s ease-in-out;
  z-index: 810; }
  .main-sidebar .sidebar .sidebar__profile {
    position: relative;
    padding-bottom: 10px;
    padding-left: 5px;
    border-bottom: 1px solid #fff; }
    .main-sidebar .sidebar .sidebar__profile:after {
      content: "";
      position: absolute;
      width: 100%;
      bottom: 0;
      left: 0;
      height: 1px;
      background: #e0e0e0; }
    .main-sidebar .sidebar .sidebar__profile .sidebar__profile__greetings {
