.cf {
  &:before, &:after {
    content: " ";
    display: table;
  }

  &:after {
    clear: both;
  }

}

.hidden {
  display: none;
}

.fl {
  float: left;
}

.fr {
  float: right;
}

.tac {
  text-align: center !important;
}

.absolute_left {
  left:0;
  right: auto;
}

.absolute_right {
  right: 0;
  left: auto;
}