.index__container {
  background: none;

  .section_header {
    border: 2px solid $color-base-link;
    font-size: 18px;
    text-transform: uppercase;
    font-weight: bold;
    text-align: center;
    padding: 3px 0;
  }

  .row {

    .rent_options {
      padding-top: 30px;
      a {
        position: relative;
        color: #fff;

        &:before {
          content: "";
          position: absolute;
          width: 100%;
          height: 175px;
          border: 2px solid #fff;
          background: rgba(9, 30, 45, .5);
          z-index: 110;
        }

        .title {
          position: absolute;
          top: -10px;
          margin: 0 auto;
          left: 0;
          right: 0;
          font-size: 20px;
          text-transform: uppercase;
          font-weight: bold;
          z-index: 150;
        }

        &:hover:before {
          border: 2px solid #27ac71;
        }

        img {
          width: 260px;
          height: 173px;
        }

      }

      p {
        color: #fff;
      }

      .caption {
        padding: 0 20px;
      }

    }

  }

  .matches, .news {
    margin-top: 30px;
    width: 30%;
    display: inline-block;

    .block_content {
      margin-top: 15px;
      text-align: center;

      .image {
        margin-bottom: 15px;
      }

    }

  }

  .news {
    margin: 30px 0 0 50px;
    width: 60%;
    vertical-align: top;

  }

}