/*
** Глобальные настройки
*/

// Colors

$color-base-link: #27ac71;
$color-base-dark-blue: #091e2d;

// Fonts

$font-base: 16px;

/*
** Sidebar
*/
// Colors

$color-sidebar-greeting: #666666;
$color-sidebar-change-avatar-text: #999999;
$color-sidebar-score-count: #27ac71;

// Fonts

$font-sidebar-greeting-size: 16px;
$font-sidebar-avatar-name-text: 18px;
$font-sidebar-score-label: 24px;
$font-sidebar-score-count: 36px;

// Specific Styles

$avatar-dimension: 60px;

/*
** Модальное окно
*/

// Colors

$color-modal-header: #f7f7f7;
$color-header-info: #4c4c4c;

// Fonts

$font-modal-header: 28px;

$font-modal-header-info: 14px;

// Line Height

$lh-header: 50px;

// Типы занятости полей
$busyness: (
        0: #9aedbd,
        25: #f2e19d,
        50: #f2e19d,
        75: #f2e19d,
        100: #f59f9f
)
